
input[type='email'],
input[type='number'],
input[type='password'],
input[type='search'],
input[type='tel'],
input[type='text'],
input[type='url'],
textarea,
select {
    font-size: 1.4rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: transparent;
    border: 0.1rem solid #d1d1d1;
    border-radius: 0.4rem;
    box-shadow: none;
    height: 3.8rem;
    padding: 0.6rem 1rem;
    width: 100%;

    &:focus{
        order: 0.1rem solid #9b4dca;
        outline: 0;
    }
}

select {
  padding: 0.6rem 3rem 0.6rem 1rem;
  background: url(data:image/svg+xml;base64,) center right no-repeat;

  &:focus {
      background-image: url(data:image/svg+xml;base64,);
  }
}


textarea {
  padding-bottom: 0.6rem;
  padding-top: 0.6rem;
  min-height: 6.5rem;
}

label,
legend {
  font-size: 1.6rem;
  font-weight: 700;
  display: block;
  margin-bottom: 0.5rem;
}

fieldset {
  border-width: 0;
  padding: 0;
}

input[type='checkbox'],
input[type='radio'] {
  display: inline;
}

.label-inline {
  font-weight: normal;
  display: inline-block;
  margin-left: 0.5rem;
}

form {
    p {
        font-size: 1.3rem;
        color: #a9a9a9;
    }
}

select {
    color: #a9a9a9;
}

option {
    color: black;
}

textarea {
    overflow: hidden;
    resize: none;
}

input[type="file"] {
    width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;

    + p:not(.error) {
        color: black;
    }
}